.page__footer {
    background-color: $color-basic-grey;
}

.partnership__group {
    background-color: #ffffff;
    display: flex;
    align-items: center;
}

.partnership__title {
    font-family: $font-title;
    margin-top: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: #000000;
}

.contacts__logo {
    text-align: center;
    margin-bottom: 32px;
}

.social__list {
    list-style: none;
    margin-top: 0;
    margin-bottom: 22px;
    padding: 22px 0 22px 0;
    display: flex;
    justify-content: space-evenly;
    border-top: 1px solid #D9D9D9;
    border-bottom: 1px solid #D9D9D9;
}

.partnership__map {
    margin-bottom: 39px;
}